.Header {
    background-color: #282c34;
    min-height: 8vh;
    display: flex;
    flex-direction: row;
    font-size: calc(10px + 2vmin);
    color: white;
    align-items: center;
  }

  .Header div.Page-Title{    
    flex-direction: column; 
    padding: 0 15px;
    margin-right: auto;
  }

  .Header div.User-Data{    
    flex-direction: column;     
    padding: 0 15px; 
  }

  .Char-Data{
    background-color: #c5ca7c;
    min-height: 4vh;
    display: flex;
    flex-direction: row;
    font-size: calc(10px + 2vmin);
    color: rgb(59, 59, 59);
    font-size: medium;
    align-items: center;    
    justify-content: center;
  }

  
  .Char-Data span{   
    color: rgb(168, 18, 18);
    font-size: medium;
    padding: 0 10px;
    font-weight: 700;
  }
  